.bottom {
	width: 100%;
	height: 100px;
	background: black;
	position: relative;

	.wrap {
		position: absolute;
		text-align: center;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);

		svg {
			margin-right: 20px;
			font: normal 20px/1 'arial';
			color: white;
		}
	}
}
